@import url(https://fonts.googleapis.com/css?family=Oxygen|Roboto+Slab&display=swap);
html,
body {
  height: 100vh;
  --title-color: #021143;
  --link-color: #0051bb;
  --link-hover-color: #1275f7;
  --list-even-color: rgba(127, 204, 233, 0.6);
  --list-odd-color: rgba(127, 204, 233, 0.3);
  --text-bg-color: rgba(255, 255, 255, 0.5);
  --header-bg-color: #5c9ad1;
  --highlight-color: #dc2e5e;
  --oxygen: 'Oxygen', sans-serif;
  --roboto-slab: 'Roboto Slab', serif;
  --text-color: #000925;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: var(--roboto-slab);
  color: var(--text-color);
  overflow-x: hidden;
}

a,
a:hover,
a:visited {
  text-decoration: none;
}

.btn_highlight_color {
  background-color: var(--highlight-color);
}

.btn_secondary_color {
  background-color: var(--header-bg-color);
}

.Header {
  height: 150px;
}

.app__main {
  height: calc(100vh - 150px);
}

@media only screen and (min-width: 750px) {
  .Header {
    height: 130px;
  }

  .app__main {
    height: calc(100vh - 130px);
  }
}

.Loading {
  display: grid;
  place-content: center;
  height: 90%;
  width: 100%;
}

.Form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 280px;
  margin: 15px auto;
  padding: 10px;
  background-color: white;
}

.Form legend {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 20px;
  align-self: flex-start;
}

.Form label,
label {
  width: 250px;
  line-height: 1.3rem;
  text-align: left;
}

.Form input,
.Form textarea,
.Form select {
  width: 250px;
  margin-top: 5px;
  margin-bottom: 20px;
  padding: 5px;
  font-size: 1rem;
  line-height: 1.2rem;
  background-color: #f5f5f5;
  border: 1px solid #b1b1b1;
}

.Form .select {
  width: 270px;
  margin-top: 5px;
  margin-bottom: 20px;
  padding: 5px;
  font-size: 1rem;
  line-height: 1.2rem;
}

.Form select {
  min-width: 250px;
}

.Form button {
  display: grid;
  place-items: center;
  font-family: 'Oxygen', sans-serif;
  font-size: 1rem;
  color: white;
  border: none;
  padding: 7px 20px 7px 20px;
  border-radius: 10px;
  margin-bottom: 15px;
}

.Form .error {
  color: red;
  line-height: 2rem;
}

@media only screen and (min-width: 750px) {
  .Form {
    width: 420px;
  }
  .Form label,
  .Form input,
  .Form textarea {
    width: 400px;
  }

  .Form select {
    min-width: 410px;
  }

  .Form .select {
    width: 420px;
  }
}

.Sidebar {
  width: 100%;
  box-sizing: border-box;
  background-color: var(--list-odd-color);
}

.Sidebar__list {
  padding: 10px;
}

.Sidebar__list li {
  padding: 5px;
}

.Sidebar__PM_header,
.Sidebar__emp_header {
  background-color: var(--list-even-color);
  padding: 10px 5px 10px 5px;
  font-size: 1rem;
  font-weight: 600;
}

.Sidebar__PM_header .fa_collapse,
.Sidebar__PM_header .fa_expand,
.Sidebar__emp_header .fa_collapse,
.Sidebar__emp_header .fa_expand {
  margin-right: 5px;
}

/*
.Sidebar__list li:nth-child(even) {
  background-color: var(--list-even-color);
}

.Sidebar__list li:nth-child(odd) {
  background-color: var(--list-odd-color);
}
*/

.ProgressBar__meter,
.ProgressBar__fill,
.ProgressBar__text {
  display: inline-block;
  width: 100%;
  height: 20px;
}

.ProgressBar__meter {
  position: relative;
  background-color: rgb(109, 202, 140);
}

.ProgressBar__fill {
  background-color: rgb(21, 190, 77);
}

.ProgressBar__text {
  position: absolute;
  left: 0;
  text-align: center;
  line-height: 20px;
}

.ProjectBar__project_container {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  padding: 10px;
  padding-top: 15px;
  font-size: 0.9rem;
}

.ProjectBar__header {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
  width: calc(100% - 110px);
}

.ProjectBar__proj_name {
  font-size: 1rem;
  font-weight: 600;
  line-height: 2rem;
}

.ProjectBar__description {
  margin-bottom: 15px;
  margin-left: 5px;
  margin-right: 5px;
}

.ProjectBar__description span {
  margin-right: 7px;
}

.ProjectBar__description_text {
  margin-top: 3px;
  padding: 10px;
  background-color: var(--text-bg-color);
}

.ProjectBar__proj_prog_date {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.ProjectBar__proj_prog {
  width: 150px;
}

.ProjectBar__deadline {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  width: 150px;
  margin-top: 5px;
}

.ProjectBar__deadline_first {
  margin-bottom: 10px;
  margin-right: 10px;
}

.ProjectBar__overdue {
  font-weight: 600;
  line-height: 1.8rem;
  color: var(--highlight-color);
}

.ProjectBar__buttons {
  position: absolute;
  top: 15px;
  right: 15px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  flex-direction: row;
  width: 100px;
}

.ProjectBar__fa {
  cursor: pointer;
  margin-left: 25px;
  font-size: 1.2rem;
}

@media only screen and (max-width: 374px) {
  .ProjectBar__project_container {
    font-size: 0.8rem;
    padding: 2px;
  }

  .ProjectBar__proj_name {
    font-size: 0.9rem;
  }

  .ProjectBar__buttons {
    position: absolute;
    top: 2px;
    right: 2px;
  }
}

@media only screen and (min-width: 1024px) {
  .ProjectBar__link_wrapper {
    display: grid;
    grid-template-columns: 25% 50% 25%;
    margin-right: 10px;
    margin-left: 5px;
    margin-top: 20px;
  }

  .ProjectBar__header {
    width: auto;
    grid-column: 1/2;
    align-self: start;
  }

  .ProjectBar__proj_name {
    font-size: 1.15rem;
  }

  .ProjectBar__proj_prog {
    margin-right: 15px;
  }

  .ProjectBar__proj_prog_date {
    flex-direction: column;
    align-items: flex-start;
    grid-column: 3/4;
    justify-self: end;
    align-self: center;
    width: 150px;
    height: 75px;
  }

  .ProjectBar__proj_prog {
    margin-bottom: 5px;
  }

  .ProjectBar__description {
    grid-column: 2/3;
    width: 100%;
    min-width: 250px;
    margin-bottom: 15px;
    margin-left: 5px;
    margin-right: 5px;
  }

  .ProjectBar__description span {
    margin-right: 7px;
  }

  .ProjectBar__description_text {
    min-height: 50px;
    margin-top: 3px;
    line-height: 1.2rem;
    background-color: rgba(255, 255, 255, 0.5);
  }

  .ProjectBar__deadline {
    display: inline-block;
    width: 150px;
  }

  .ProjectBar__deadline_first {
    margin-right: 7px;
  }

  .ProjectBar__overdue {
    line-height: 2.4rem;
  }

  .ProjectBar__buttons {
    top: 10px;
    right: 10px;
  }

  .ProjectBar__fa {
    font-size: 1.4rem;
  }
}

.Dashboard__main {
  box-sizing: border-box;
}

.Dashboard__project_header {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding-left: 15px;
  padding-right: 15px;
  font-family: var(--oxygen);
  font-size: 1.3rem;
  font-weight: 600;
  background-color: var(--header-bg-color);
}

.Dashboard__new {
  height: 35px;
  width: 70px;
  font-family: var(--oxygen);
  font-size: 1.1rem;
  color: white;
  border: none;
  border-radius: 10px;
  background-color: var(--highlight-color);
}

.Dashboard__list {
  list-style: none;
}

.Dashboard__list li:nth-child(even) .ProjectBar__project_container {
  background-color: var(--list-even-color);
}

.Dashboard__list li:nth-child(odd) .ProjectBar__project_container {
  background-color: var(--list-odd-color);
}

.Dashboard__no_projects {
  display: grid;
  place-content: center;
  width: 100%;
  margin: 50px auto;
}

.Dashboard__welcome {
  display: block;
  font-family: var(--oxygen);
  font-size: 2rem;
  margin-bottom: 20px;
}

.Dashboard__container a,
.Dashboard__container a:hover,
.Dashboard__container a:visited {
  color: var(--text-color);
}

@media only screen and (min-width: 750px) {
  .Dashboard__container {
    position: relative;
    width: 100%;
    margin: 0 auto;
  }

  .Dashboard__main {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
  }

  .Dashboard__projects {
    width: calc(100% - 200px);
  }
}

.Header {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fff;
}

.Header h1 {
  margin-bottom: 100px;
}

.Header__link {
  display: flex;
  align-items: center;
  margin-left: 15px;
}

.Header__logo {
  display: none;
}

.Header__app_name {
  margin-left: 20px;
  font-family: var(--oxygen);
  font-size: 2.8rem;
  color: var(--title-color);
}

.Header__user_info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-left: 15px;
  margin-right: 15px;
}

.Header__sub_container {
  position: absolute;
  bottom: 5px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Header__login {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.Header__btn {
  display: grid;
  place-items: center;
  width: 85px;
  height: 30px;
  font-family: 'Oxygen', sans-serif;
  font-size: 1rem;
  color: white;
  border-radius: 10px;
}

.Header__primary {
  background-color: var(--highlight-color);
}

.Header__alt {
  background-color: #638ab7;
}

.Header__dashboard {
  font-family: var(--oxygen);
  font-weight: 600;
  font-size: 1rem;
  color: var(--highlight-color);
}

.Header__db_logout {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  right: 15px;
  bottom: 10px;
  width: 90px;
  height: 60px;
}

.Header__db_logout h3 {
  margin: 0;
}

.Header__notifications {
  position: absolute;
  right: -20px;
  margin-top: 10px;
  z-index: 10;
}

.Header__notifications button {
  margin-right: 20px;
}

@media only screen and (min-width: 750px) {
  .Header {
    flex-direction: row;
    justify-content: space-between;
  }

  .Header h1 {
    position: absolute;
    top: 13pt;
    left: 0;
  }

  .Header__link {
    margin-left: 45px;
  }

  .Header__logo {
    display: inline-block;
    position: absolute;
    top: -2pt;
    left: 10pt;
    width: 140px;
    height: auto;
  }

  .Header__app_name {
    position: absolute;
    top: 20pt;
    left: 85pt;
    font-size: 2.4rem;
  }

  .Header__login {
    position: absolute;
    right: 30px;
    top: 5px;
    width: 150px;
    height: 100px;
    flex-direction: column;
    align-items: space-between;
  }

  .Header__sub_container {
    position: absolute;
    top: 0;
    right: 0;
    width: auto;
    margin-right: 15px;
  }

  .Header__user_info {
    margin-right: 30px;
    line-height: 1.4rem;
  }

  .Header__db_logout {
    height: 65px;
    right: 0px;
    top: 0px;
  }

  .Header__dashboard {
    font-size: 1rem;
  }

  .Header__notifications {
    margin-top: 0;
  }
}

.newjob__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1em;
}

.newjob__form .input {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.newjob__form .input input,
.newjob__form .input textarea {
  flex-basis: 80%;
}

.JobNotification__btn {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  padding: 10px 15px 10px 15px;
  border: none;
  font-family: var(--oxygen);
  font-size: 1rem;
  line-height: 1.2rem;
  text-align: right;
  color: white;
  background-color: #262b3b;
}

.JobNotification__approval {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.notification_button {
  display: grid;
  place-items: center;
  font-family: 'Oxygen', sans-serif;
  font-size: 1rem;
  color: white;
  border: none;
  padding: 7px 20px 7px 20px;
  border-radius: 10px;
  margin-bottom: 15px;
  margin-top: 10px;
  width: 100%;
}

.JobNotification__icon {
  position: absolute;
  left: 5px;
  font-size: 1.3rem;
}

.JobNotification__number {
  display: grid;
  place-content: center;
  width: 30px;
  height: 30px;
  padding: 0;
  margin-right: 30px;
  margin-left: 10px;
  font-size: 1.2rem;
  font-weight: 600;
  color: white;
  border-radius: 50%;
  background-color: var(--highlight-color);
}

.JobNotification__list {
  padding: 5px 20px 5px 15px;
  border: 1px solid grey;
  background-color: white;
}

.JobNotification__list li {
  display: flex;
  justify-content: space-between;
  padding-bottom: 7px;
  padding-top: 7px;
  border-bottom: 1px solid grey;
  flex-direction: row;
}

.JobNotification__info {
  display: inline-block;
}

.JobNotification__close {
  cursor: pointer;
  margin-left: 15px;
  margin-right: 15px;
  font-size: 1.3rem;
  color: var(--title-color);
}

.JobNotification__list li:first-child {
  border-bottom: 1px solid grey;
}

.JobNotification__list li:last-child {
  border-bottom: none;
}

.radio-toolbar {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.radio-toolbar input[type='radio'] {
  opacity: 0;
  position: fixed;
  width: 0;
}

.radio-toolbar label {
  display: inline-block;
  background-color: var(--list-odd-color);
  padding: 5px;
  max-width: 130px;
  margin: 10px;
  font-size: 1.1em;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
}

.radio-toolbar input[type='radio']:checked + label {
  background-color: var(--header-bg-color);
  color: #f4f2f3;
}

@media only screen and (min-width: 750px) {
  .Login {
    top: 130px;
    height: calc(100% - 130px);
  }

  .Register .LoginRegister {
    position: fixed;
    top: 250px;
    left: calc(50% - 235px);
  }

  img {
    left: 10%;
    height: 45%;
  }

  .radio-toolbar {
    flex-direction: row;
  }
}

#projectView__container {
  display: flex;
  flex-direction: row;
}

.ProjectView__header {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 1em;
  margin-top: 10px;
  margin-bottom: 30px;
  background-color: rgba(127, 204, 233, 0.8);
}

.ProjectView__jobs_header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  font-family: var(--oxygen);
  font-size: 1.2rem;
  font-weight: 600;

  background-color: var(--header-bg-color);
}

.ProjectView__add {
  height: 35px;
  width: 100px;
  font-family: var(--oxygen);
  font-size: 1.1rem;
  color: white;
  border: none;
  border-radius: 10px;
  background-color: var(--highlight-color);
}

#project_progress {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-basis: 20%;
}

#jobs_stats_container {
  display: flex;
  flex-direction: column;
  flex-basis: 80%;
  margin: 5px;
}

.ProjectBar__project_container {
  background-color: var(--list-even-color);
}

.ProjectBar__project_container a {
  color: black;
}

.App__fa_h1 h3 {
  margin-left: 5px;
}

.ProjectView__stats_container {
  padding: 15px;
  background-color: var(--list-odd-color);
}

.ProjectView__stats_container canvas {
  width: 320px !important;
  height: 200px !important;
  margin: 0 auto;
}

@media only screen and (max-width: 374px) {
  .ProjectView__stats_container canvas {
    width: 230px !important;
    height: 170px !important;
  }
}

@media only screen and (min-width: 750px) {
  #projectView_main {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
  }

  .ProjectView__jobs_stats {
    width: calc(100% - 200px);
  }

  .ProjectView__stats_container canvas {
    width: auto !important;
    height: 240px !important;
    margin: 0 auto;
  }
}

.Jobs__list .JobItem:nth-child(even) {
  background-color: var(--list-even-color);
}

.Jobs__list .JobItem:nth-child(odd) {
  background-color: var(--list-odd-color);
}

.JobItem {
  display: flex;
  flex-direction: column;
}

.JobItem__container {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 5% 65% 30%;
  grid-template-rows: 20% 50% 30%;
  align-items: start;
  padding: 10px;
}

.JobItem__icon {
  grid-column: 1/2;
  grid-row: 1/2;
}

.JobItem__name {
  grid-column: 2/3;
  grid-row: 1/2;
  font-size: 1.1rem;
  font-weight: 600;
}

.JobItem__details {
  grid-column: 1/4;
  grid-row: 2/3;
  align-self: center;
  padding: 15px 0 25px 0;
}

.JobItem__details_text {
  padding: 5px;
}

.JobItem__progress {
  grid-column: 1/4;
  grid-row: 3/4;
  justify-self: center;
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-left: 10px;
  margin-right: 10px;
}

.JobItem__meter_container {
  width: 150px;
}

.ProgressBar__meter {
  height: 20px;
  min-width: 150px;
  max-width: 150px;
  margin-top: 3px;
}

.JobItem__date_etc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  min-width: 150px;
  height: auto;
}

.JobItem__date_etc span {
  line-height: 1.5rem;
}

.JobItem__revisions_requested {
  display: block;
  margin-top: 10px;
  font-weight: 600;
  color: var(--highlight-color);
}

.JobItem__buttons {
  grid-column: 3/4;
  grid-row: 1/2;
  align-self: start;
  justify-self: end;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.JobItem__fa,
.JobItem__fa_bigger {
  display: grid;
  place-content: center;
  width: 30px;
  height: 30px;
  color: var(--title-color);
}

.JobItem__fa {
  font-size: 1.2rem;
}

.JobItem__fa_bigger {
  font-size: 1.5rem;
}

.JobItem__fa:hover {
  cursor: pointer;
}

.JobItem canvas {
  box-sizing: border-box;
  min-width: 200px !important;
  min-height: 300px !important;
  max-width: 400px !important;
  max-height: 600px !important;
  height: auto !important;

  margin: 0 auto;
  padding: 20px;
}

.JobItem__form {
  grid-column: 1/4;
  grid-row-start: 4;
}

.JobItem__assigned_employees {
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
  padding: 10px;
  border-top: 1px solid grey;
}

.JobItem__assigned_employees span {
  display: block;
  margin-bottom: 10px;
  font-weight: 600;
}

.JobItem__assigned_employees ul li {
  padding: 5px;
}

@media only screen and (max-width: 374px) {
  .JobItem__date_etc {
    min-width: 0;
  }
}

@media only screen and (min-width: 1024px) {
  .JobItem__container {
    grid-template-columns: 2% 15% calc(100% - 180px - 29%) 180px 12%;
    grid-template-rows: 100%;
    align-content: start;
  }

  .JobItem__icon {
    grid-column: 1/2;
  }

  .JobItem__name {
    grid-column: 2/3;
    font-size: 1.1rem;
    font-weight: 600;
  }

  .JobItem__details {
    grid-column: 3/4;
    grid-row: 1/2;
    padding-top: 0;
  }

  .JobItem__progress {
    grid-column: 4/5;
    grid-row: 1/2;
    flex-direction: column;
    align-items: flex-start;
  }

  .JobItem__date {
    display: block;
    margin-left: 0;
    margin-top: 10px;
  }

  .JobItem__buttons {
